@-webkit-keyframes progressbar {
    0%     	{
		 width: 0%;
		 background-color: darken(red, 20%);
	 }
	 10%    	{
		 width: 34%;
	 }
	 21%   	{
		 width: 40%;
	 }
	 29%   	{
		 width: 45%;
		 background-color: darken(yellow, 8%);
	 }
	 57%   	{
		 width: 64%;
	 }
    70%  	{
		 width: 70%;
	 }
    84%  	{
		 width: 70%;
	 }
    100% 	{
		 width: 100%;
		 background-color: darken(green, 3%);
	}
}
